body {
  text-align: center;
  background-color: #e9967a;
}

.breed-select {
  max-width: 400px;
  margin: 0 auto;
}

.loader {
  width: 78px;
  height: 78px;
  box-sizing: border-box;
  background: #fff;
  border: 8px solid #131a1d;
  border-radius: 50%;
  margin: 350px auto 0;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 5;
  box-sizing: border-box;
  background: #263238;
  border-bottom: 8px solid #131a1d;
  animation: eyeShade 3s infinite;
  position: absolute;
  top: -50%;
  left: 0;
}

.loader:before {
  content: "";
  width: 32px;
  z-index: 2;
  height: 32px;
  background: #111;
  border-radius: 50%;
  animation: eyeMove 3s infinite;
  position: absolute;
  bottom: 15px;
  left: 20px;
}

@keyframes eyeShade {
  0% {
    transform: translateY(0);
  }

  20% {
    transform: translateY(5px);
  }

  40%, 50% {
    transform: translateY(-5px);
  }

  60% {
    transform: translateY(-8px);
  }

  75% {
    transform: translateY(5px);
  }

  100% {
    transform: translateY(10px);
  }
}

@keyframes eyeMove {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(0, 5px);
  }

  40%, 50% {
    transform: translate(0, -5px);
  }

  60% {
    transform: translate(-10px, -5px);
  }

  75% {
    transform: translate(-20px, 5px);
  }

  100% {
    transform: translate(0, 10px);
  }
}

.cat-img {
  border-radius: 15px;
  margin-top: 15px;
}

